import { NextResponse } from 'next/server';
import axios from 'axios';

export async function POST(request: Request) {
  const body = await request.json();
  const { login, password } = body;
  const apiUrl = process.env.NEXT_PUBLIC_API_URL;

  try {
    const odooResponse = await axios.post(
      `${apiUrl}/api/contact_us`,
      { login, password },
      {
        withCredentials: true,
      }
    );

    const setCookie = odooResponse.headers['set-cookie'];

    if (!setCookie) {
      return NextResponse.json({ message: 'No session returned from Odoo' }, { status: 401 });
    }

    const response = NextResponse.json({ message: 'Request successful' });

    response.headers.set('set-cookie', setCookie[0]);

    return response;
  } catch (error: any) {
    return NextResponse.json({ message: 'Request failed' }, { status: 401 });
  }
}
